import Vue from "vue"
import VueRouter from "vue-router"
import AnyPage from "../components/AnyPage"
import FirstVersion from "../components/FirstVersion"

Vue.use(VueRouter)

const routes = [
  {
    path: "*",
    name: "AnyPage",
    component: AnyPage
  },
  {
    path: "/",
    name: "FirstVersion",
    component: FirstVersion
  }
];

const router = new VueRouter({
  mode: "history",
  base: process.env.BASE_URL,
  routes,
});

export default router;
